// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import MuseUI from 'muse-ui';
import 'muse-ui/dist/muse-ui.css';
import './assets/css/main.css'; //引入全局样式
import Toast from 'muse-ui-toast'; // 消息提示
import VueClipboard from 'vue-clipboard2' //复制粘贴复制插件

axios.defaults.baseURL='https://api.hibi.com';
axios.defaults.headers.common['Content-Type'] = 'application/json-patch+json';
const token = sessionStorage.getItem('accessToken') || 'null';
axios.defaults.headers.common.authorization = `Bearer ${token}`;
axios.defaults.headers.common['abp-tenantid'] = '1';

Vue.prototype.$http = axios;

Vue.use(MuseUI)
Vue.use(Toast,{
  position: 'bottom',               // 弹出的位置
  time: 3000,                       // 显示的时长
  closeIcon: 'close',               // 关闭的图标
  close: false,                      // 是否显示关闭按钮
  successIcon: 'check_circle',      // 成功信息图标
  infoIcon: 'info',                 // 信息信息图标
  warningIcon: 'priority_high',     // 提醒信息图标
  errorIcon: 'warning'              // 错误信息图标
})
Vue.use(VueClipboard)

Vue.config.productionTip = false

//路由全局守卫
router.beforeEach((to,from,next)=>{
  const token = sessionStorage.getItem("accessToken")
  // 如果用户已经登录过，或没登陆过在登陆页面
  if(to.path === '/Login' || token || to.path === '/Register' || to.path === '/Forget'){
    next()
  }else{
    next("/Login")
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
})



