<template>
  <div class="deal" v-if="market.baseCoin">
    <div class="top2">
      <div class="title"><router-link to="/ChooseCoin">{{market.baseCoin.name}}/{{market.quoteCoin.name}}<span class="icon iconfont">&#xeb15;</span></router-link></div>
      <span class="icon iconfont">&#xe627;</span>
      <mu-button icon @click="toDetails" slot="left">
        <span class="icon iconfont">&#xe619;</span>
      </mu-button>
    </div>
    <!-- info -->
    <div class="deal-info">
      <!-- 成交信息 -->
      <div class="left">
        <div class="select">
          <mu-select class="select-reset" v-model="fractional" :change="fractionalChange()">
            <mu-option v-for="(item,index) in fractionalList" :key="index" :label="item" :value="item"></mu-option>
          </mu-select>
          <mu-select class="select-reset" v-model="buySell" :change="buySellChange()">
            <mu-option v-for="(item,index) in buySellList" :key="index" :label="item" :value="item"></mu-option>
          </mu-select>
        </div>
        <ul>
          <li class="title"><span>价格({{market.quoteCoin.name}})</span><span>数量({{market.baseCoin.name}})</span></li>
          <li v-for="(item,index) in bids" :key="index">
            <span>{{item[0]}}</span><span>{{item[1]}}</span>
          </li>
        </ul>
        <div class="price"><span>均价：{{(ticker.high+ticker.low)/2}}</span>
        <!-- ￥1,256.89 -->
        </div>
      </div>
      <div class="right">
        <mu-container>
          <mu-tabs :value.sync="active">
            <mu-tab style="color:#38BA54;">买入</mu-tab>
            <mu-tab style="color:#FF3C00;">卖出</mu-tab>
          </mu-tabs>
          <div v-if="active === 0">
            <!-- 做成组件 -->
            <div class="addInput">
              <mu-text-field type="number" v-model="price" placeholder="价格" solo class="demo-divider-form"></mu-text-field>
              <span class="icon iconfont" @click="priceMinus">&#xe6ca;</span><span class="icon iconfont" @click="priceAdd">&#xe6df;</span>
            </div>
            <!-- <div class="price2">≈￥1,256.89</div> -->
            <div class="addInput">
              <mu-text-field type="number" v-model="count" placeholder="数量" min=0 solo class="demo-divider-form"></mu-text-field>
              <span class="icon iconfont" @click="countMinus">&#xe6ca;</span><span class="icon iconfont" @click="countAdd">&#xe6df;</span>
            </div>
            <div class="select-control-group">
              <mu-flex class="select-control-row" :key="item" v-for="(item) in checkNumberList">
                <mu-radio :value="item" v-model="checkNumber"  :label="item"></mu-radio>
              </mu-flex>
            </div>
            <div class="total">
              <span>交易额：{{totalPrice}}</span>
              <span v-if="coinInfo.banlance">{{market.quoteCoin.name}}可用：
                {{coinInfo.banlance.toFixed(coinInfo.virtualCoin.decimals)}}</span>
            </div>
            <mu-button color="primary" @click="buySubmit(true)">买入{{market.baseCoin.name}}</mu-button>
          </div>
          <div v-if="active === 1">
            <!-- 做成组件 -->
            <div class="addInput">
              <mu-text-field type="number" v-model="price" placeholder="价格" solo class="demo-divider-form"></mu-text-field>
              <span class="icon iconfont" @click="priceMinus">&#xe6ca;</span><span class="icon iconfont" @click="priceAdd">&#xe6df;</span>
            </div>
            <!-- <div class="price2">≈￥1,256.89</div> -->
            <div class="addInput">
              <mu-text-field type="number" v-model="count" placeholder="数量" min=0 solo class="demo-divider-form"></mu-text-field>
              <span class="icon iconfont" @click="countMinus">&#xe6ca;</span><span class="icon iconfont" @click="countAdd">&#xe6df;</span>
            </div>
            <div class="select-control-group">
              <mu-flex class="select-control-row" :key="item" v-for="(item) in checkNumberList">
                <mu-radio :value="item" v-model="checkNumber"  :label="item"></mu-radio>
              </mu-flex>
            </div>
            <div class="total">
              <span>交易额：{{totalPrice}}</span>
              <span v-if="coinInfo2.banlance">{{market.baseCoin.name}}可用：{{coinInfo2.banlance.toFixed(coinInfo2.virtualCoin.decimals)}}</span>
            </div>
            <mu-button color="primary" class="sellButton" @click="buySubmit()">卖出{{market.baseCoin.name}}</mu-button>
          </div>
          <mu-select class="select-reset" v-model="priceName" :change="priceChange(priceName)">
            <mu-option v-for="(item,index) in priceList" :key="index" :label="item" :value="item"></mu-option>
          </mu-select>
        </mu-container>
      </div>
    </div>
    <!-- entrustInfo -->
    <div class="entrustTop">
      <div class="left">
        <h3>当前委托</h3>
        <mu-flex class="select-control-row">
          <mu-switch v-model="switchVal" label="隐藏其它交易对" label-left></mu-switch>
        </mu-flex>
      </div>
      <div class="right">
        <!-- <div><span class="icon iconfont" @click="allCancel">&#xe646;</span>全部撤销</div> -->
        <router-link to="/Entrust"><span class="icon iconfont">&#xe60f;</span>历史委托</router-link>
      </div>
    </div>
    <!-- entrustList -->
    <ul class="entrustList">
      <li class="title"><span>交易对</span><span>价格</span><span>交易额</span></li>
      <li v-for="item in entrustOrdersFilter" :key="item.id">
        <span>{{item.baseCoinName}}/{{item.quoteCoinName}}</span>
        <span>{{item.price}}</span>
        <span>{{item.amount}}</span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Deal',
  data () {
    return {
      active: 0,
      market: {}, //当前交易对的信息
      ticker: {}, // 当前交易对的价格信息
      coinInfo: {}, // 钱包信息
      entrustOrders: {}, // 当前委托
      trades: {}, // 最新成交
      price: 0, // 买入卖出价格
      count: 0, // 买入卖出数量
      decimalCount: 1, // 存储小数位数
      asks:[], // 买入深度数据
      bids:[], // 卖出深度数据
      fractional:"6位小数",
      fractionalList:["6位小数","5位小数","4位小数","3位小数"],
      buySell:"买/卖",
      buySellList:["买/卖","买","卖"],
      priceName:"限价",
      priceList:["限价","市价"],
      checkNumber:"",
      checkNumberList:["25%","50%","75%","100%"],
      switchVal: false,
      normal: {
        checkbox: true,
        radio: 1,
        switch: false
      }
    }
  },
  props:['id'],
  computed: {
    // 买入卖出交易额
    totalPrice() {
      return parseFloat(this.price * this.count).toFixed(this.decimalCount);
    },
    entrustOrdersFilter() {
      if(this.entrustOrders.length>0){
        return this.entrustOrders.filter((item) => {
          let flag = true;
          if (this.switchVal) {
            flag = item.marketId == this.ticker.marketId;
          }
          return flag;
        });
      }
    }
  },
  watch: {
    price(value){
      if(value < 0){
        this.price = 0;
      }
    },
    count(value){
      if(value < 0){
        this.count = 0;
      }
    },
  },
  created() {
    if(!this.id){
      this.id = 1;
    }
    this.getInfo();
  },
  methods: {
    getMarkets() {
      return this.$http.get('/api/services/app/Market/GetMarkets');
    },
    getTickers() {
      return this.$http
        .get('/api/services/app/Market/GetTickers', {
          params: {
            TimeZone: 'UTC',
          },
        });
    },
    // 请求数据
    getInfo() {
      this.$http.all([this.getMarkets(), this.getTickers()])
        .then(this.$http.spread((markets, tickers) => {
            const coinTickers = tickers.data.result.items;
            const marketsArray = markets.data.result.items;
            this.market = marketsArray.find((item, index, arr) => {
            return item.id == this.id
            })
            this.ticker = coinTickers.find((item, index, arr) => {
            return item.marketId == this.id
            })
            this.price = this.ticker.close;
            this.getCoinInfo();
            this.getEntrustOrders();
            // 计算小数位数
            this.decimalCount = this.price.toString().split(".")[1].length; 
            console.log(this.ticker)
            console.log(this.market)
            // this.getTrades();
            this.getDepth();
        }))
        .catch((error) => {
            this.$toast.message(error.response.data.error.message);
        });
    },
    getCoinInfo() {
      this.$http('/api/services/app/Wallet/GetMyWalletBalances')
      .then((response) => {
        const virtualCoinList = response.data.result.items;
        this.coinInfo = virtualCoinList.find((item, index, arr) => {
          return item.visualCoinId == this.market.quoteCoinId
        })
        this.coinInfo2 = virtualCoinList.find((item, index, arr) => {
          return item.visualCoinId == this.market.baseCoinId
        })
        console.log(this.coinInfo2)
      })
      .catch((error) => {
        this.$toast.message(error.response.data.error.message);
      });
    },
    // 最新成交
    getTrades() {
      const time = new Date().toISOString();
      const param = {
        marketTenantId: this.ticker.marketTenantId,
        marketId: this.ticker.marketId,
        time: time,
      }
      this.$http.get('/api/services/app/Market/GetTrades', param)
      .then((response) => {
        console.log(response.data.result.items)
        this.trades = response.data.result.items.reverse();
        this.trades.map((element) => {
          const date = new Date(element.creationTime);
          element.time = `${(`00${date.getHours()}`.slice(-2))}:${(`00${date.getMinutes()}`.slice(-2))}:${(`00${date.getSeconds()}`.slice(-2))}`;
          return element;
        });
      })
    },
    // 深度数据
    getDepth() {
      const param = {
        marketTenantId: this.ticker.marketTenantId,
        marketId: this.ticker.marketId,
      }
      this.$http.get(`/api/services/app/Market/GetDepth?marketTenantId=${this.ticker.marketTenantId}&marketId=${this.ticker.marketId}`)
      .then((response) => {
        console.log(response.data.result.bids)
        this.bids = response.data.result.bids;
      })
      .catch((error) => {
        this.$toast.message(error.response.data.error.message);
      });
    },
    // 当前委托
    getEntrustOrders() {
      const param = {
        marketTenantId: this.marketTenantId,
        marketId: this.marketId,
        skipCount: 0,
        maxResultCount: 10,
      }; 
      this.$http("/api/services/app/Trade/GetMyOrders", param).then((response) => {
        this.entrustOrders = response.data.result.items;
        console.log(this.entrustOrders)
      })
    },
    // 跳转详情
    toDetails(id) {
      this.$router.push(`/Details/${this.id}`)
    },
    // 价格加减
    priceAdd() {
      this.price = parseFloat((this.price + Math.pow(10,-this.decimalCount)).toFixed(this.decimalCount));
    },
    priceMinus() {
      this.price = parseFloat((this.price - Math.pow(10,-this.decimalCount)).toFixed(this.decimalCount));
    },
    // 数量加减
    countAdd() {
      this.count++;
    },
    countMinus() {
      this.count--;
    },
    // 买入,卖出
    buySubmit(isBuy) {
      if(this.price <= 0 || this.count <= 0){
        this.$toast.message("请输入合理的数值！");
        return;
      }
      if (this.totalPrice > this.coinInfo.banlance) {
        this.$toast.message("对不起, 您的货币余额不足!");
        return;
      }
      let apiURL;
      let price = this.price;
      let amount;
      if (isBuy) {
        apiURL = '/api/services/app/Trade/CreateEntrustOfLimitBuy';
        amount = this.count*this.price;
      } else {
        apiURL = '/api/services/app/Trade/CreateEntrustOfLimitSell';
        amount = this.count;
      }
      const param = {
        marketTenantId: this.ticker.marketTenantId,
        marketId: this.ticker.marketId,
        price,
        amount,
      }
      console.log(param)
      this.$http.post(apiURL, param).then((response) => {
        this.$toast.message("委托成功!");
        this.count = 0;
        this.getEntrustOrders();
      })
      .catch((error) => {
        this.$toast.message(error.response.data.error.message);
      });
    },
    changeChoose(index) {
      // 是否选择
    },
    // 小数选择
    fractionalChange() {
      // console.log(this.fractional)
    },
    // 买卖选择
    buySellChange() {
      // console.log(this.buySell)
    },
    // 限价市价选择
    priceChange() {

    },
    // 全部撤销
    allCancel() {

    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.sellButton{ background-color: #FF3C00; opacity: 0.9;}
.left ul{ max-height: 264px; overflow-y: auto;}
</style>



