<template>
  <div class="historys">
    <mu-tabs :value.sync="active" full-width>
      <mu-tab>充值</mu-tab>
      <mu-tab>提现</mu-tab>
    </mu-tabs>
    <div v-if="active === 0">
      <ul>
        <li v-if="rechargeList.amount" v-for="item in rechargeList" :key="item.id">
          <div class="htip">
            <!-- <span>EOS/USDT</span> -->
            <span v-if="item.status == '5'">等待区块确认</span>
            <span v-if="item.status == '6'">成功</span>
            <span v-if="item.status == '3'">取消</span>
            <span v-if="item.status == '3'">等待人工审核</span>
          </div>
          <div class="title"><span>时间</span><span>充值数量（USDT）</span></div>
          <div><span>{{item.cancelledTime | formatDate}}</span><span>{{item.amount}}</span></div>
          <div class="title"><span>充值源地址</span><span>交易ID</span></div>
          <div><span>{{item.address}}</span><span>{{item.txHash}}</span></div>
        </li>
      </ul>
    </div>
    <div v-if="active === 1">
      <ul>
        <li v-if="rechargeList.amount" v-for="item in rechargeList" :key="item.id">
          <div class="htip">
            <!-- <span>EOS/USDT</span> -->
            <span v-if="item.status == '5'">等待区块确认</span>
            <span v-if="item.status == '6'">成功</span>
            <span v-if="item.status == '3'">取消</span>
            <span v-if="item.status == '3'">等待人工审核</span>
          </div>
          <div class="title"><span>时间</span><span>提现数量（USDT）</span></div>
          <div><span>{{item.cancelledTime | formatDate}}</span><span>{{item.amount}}</span></div>
          <div class="title"><span>提现地址</span><span>交易ID</span></div>
          <div><span>{{item.address}}</span><span>{{item.txHash}}</span></div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import formatDate from '../bin/date.js';
export default {
  name: 'Historys',
  data () {
    return {
      active: 0,
      rechargeList: [],
      cashList:[],
    }
  },
  props:['id'],
  created() {
    this.queryRecord();
  },
  methods: {
    getRechargeHistory(param) {
      return this.$http('/api/services/app/Wallet/GetMyDepositHistory',{ params: param})
    },
    getCashHistory(param) {
      return this.$http('/api/services/app/Wallet/GetMyWithdrawHistory', { params: param})
    },
    // 获取记录
    queryRecord() {
      const param = {
        WalletId: this.id,
        SkipCount: 0,
        MaxResultCount: 10,
      };
      this.$http.all([this.getRechargeHistory(param), this.getCashHistory(param)])
      .then(this.$http.spread((recharge, cash) => {
        this.rechargeList = recharge.data.result.items;
        // this.cashList = cash.data.result.items;
      }))
      // .catch((error) => {
      //   this.$toast.message(error.response.data.error.message);
      // });
    },
  },
  filters: {
    formatDate(time) {
      const date = new Date(time);
      return formatDate(date, 'yyyy-MM-dd hh:mm');
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



