<template>
  <!-- 登陆 -->
  <div class="register">
    <div class="logo"><img src="../../assets/images/logo.png" alt=""></div>
    <mu-form ref="form" :model="validateForm" class="login-content">
      <mu-form-item label="输入邮箱" prop="username" :rules="usernameRules">
        <mu-text-field v-model="validateForm.username" prop="username"></mu-text-field>
      </mu-form-item>
      <mu-form-item label="输入验证码" prop="smscode" :rules="smscodeRules">
        <sms-code v-model="validateForm.smscode" :phone="validateForm.username" @getCode="getCode" prop="smscode"></sms-code>
      </mu-form-item>
      <mu-form-item label="输入新密码" prop="password" :rules="passwordRules">
        <mu-text-field type="password" v-model="validateForm.password" prop="password"></mu-text-field>
      </mu-form-item>
      <mu-form-item>
        <mu-button color="primary" @click="submit">确定</mu-button>
      </mu-form-item>
    </mu-form>
    <div class="login-bottom">
      已注册? <router-link to="/Login">登陆</router-link>
    </div>
  </div>
</template>

<script>
import smsCode from "@/components/bin/smsCode";
export default {
  name: 'Login',
  components: {
    smsCode
  },
  data () {
    return {
      usernameRules: [
        { validate: (val) => !!val, message: '必须填写手机号或邮箱'},
        { validate: (val) => val.length >= 4, message: '用户名长度大于4'}
      ],
      passwordRules: [
        { validate: (val) => !!val, message: '必须填写密码'},
        { validate: (val) => val.length >= 6 && val.length <= 20, message: '密码长度大于6小于20'}
      ],
      smscodeRules: [
        { validate: (val) => !!val, message: '必须填写验证码'},
      ],
      validateForm: {
        username: '',
        password: '',
        smscode: '',
      }
    }
  },
  methods: {
    getCode() {
      let app = this;
      this.$http({
        // url: app.$lib.url + "index/sendSms",
        // data: {
        //   // act: "send_code",
        //   phone: app.username
        // },
        // success: function(r) {
        //   if (r.status == "y") {
        //     app.serverCode = r.data;
        //     localStorage.serverCode = r.data;
        //   } else {
        //     app.$layer.open({ time: 3, skin: "msg", content: r.info });
        //   }
        // }
      });
    },
    submit() {
      this.$refs.form.validate().then((result) => {
        console.log('form valid: ', result)
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login{ margin-top: 20px;}
</style>



