<template>
  <div class="qusetion">
    <mu-form ref="form" :model="validateForm" class="card-content">
      <mu-form-item label="请描述您的问题" prop="question" :rules="questionRules">
        <mu-text-field v-model="validateForm.question" multi-line :rows="5" full-width></mu-text-field><br/>
      </mu-form-item>
      <mu-form-item>
        <mu-button color="primary" @click="submit">反馈问题</mu-button>
        <mu-button color="primary" to="/QusetionList">我的反馈</mu-button>
      </mu-form-item>
    </mu-form>
  </div>
</template>

<script>
export default {
  name: 'Qusetion',
  data () {
    return {
      questionRules: [
        { validate: (val) => !!val, message: '内容不能为空'},
        { validate: (val) => val.length >= 10 && val.length <= 240, message: '内容长度大于10小于240'}
      ],
      validateForm: {
        question: ''
      }
    }
  },
  methods: {
    submit() {
      this.$refs.form.validate().then((result) => {
        if(result) {

        }
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



