import Vue from 'vue'
import Router from 'vue-router'

import Login from '@/components/login/Login'
import Register from '@/components/login/Register'
import Forget from '@/components/login/Forget'

import Index from '@/components/Index'
import ChooseCoin from '@/components/ChooseCoin'
import Search from '@/components/Search'
import Details from '@/components/Details'
import Deal from '@/components/Deal'
import Entrust from '@/components/Entrust'
import EntrustDetails from '@/components/EntrustDetails'
// 个人中心
import MyCenter from '@/components/mycenter/Index'
import Card from '@/components/mycenter/Card'
import Qusetion from '@/components/mycenter/Qusetion'
import QusetionList from '@/components/mycenter/QusetionList'
import AboutUs from '@/components/mycenter/AboutUs'
import About from '@/components/mycenter/About'
import Contact from '@/components/mycenter/Contact'
import Notice from '@/components/mycenter/Notice'
import Safe from '@/components/mycenter/Safe'
import ResetPassWord from '@/components/mycenter/ResetPassWord'
import EmailVerify from '@/components/mycenter/EmailVerify'
import PhoneVerify from '@/components/mycenter/PhoneVerify'
import GoogleVerify from '@/components/mycenter/GoogleVerify'
// 资产
import Property from '@/components/property/Index'
import Recharge from '@/components/property/Recharge'
import Cash from '@/components/property/Cash'
import Address from '@/components/property/Address'
import Historys from '@/components/property/Historys'



Vue.use(Router)

export default new Router({
  routes: [
    { path: '/', name: 'home', redirect:"/Index" },
    { path: '/Index', name: '行情', component: Index, meta: { keepAlive: true} },
    { path: '/Login', name: '登陆', component: Login, meta: { keepAlive: true},
      beforeEnter:(to,from,next)=>{
        // 如果用户已经登录过,则转到用户列表页面
        const token = sessionStorage.getItem("accessToken");
        if(token){
          next("/Index");
        }else{
          next()
        }
      }
    },
    { path: '/Register', name: '注册', component: Register, meta: { keepAlive: true}},
    { path: '/Forget', name: '忘记密码', component: Forget, meta: { keepAlive: true} },
    { path: '/Deal', name: '交易', component: Deal, },
    { path: '/Deal/:id', name: '币种交易', component: Deal, props: true,},
    { path: '/ChooseCoin', name: '选择币种', component: ChooseCoin, meta: { keepAlive: true} },
    { path: '/Search', name: '搜索', component: Search, meta: { keepAlive: true} },
    { path: '/Details/:id', name: '详情', component: Details, props: true },
    { path: '/Entrust', name: '历史委托', component: Entrust },
    { path: '/EntrustDetails/:id', name: '成交明细', component: EntrustDetails, props: true },
    { path: '/MyCenter', name: '个人中心', component: MyCenter },
    { path: '/Card', name: '身份认证', component: Card, meta: { keepAlive: true} },
    { path: '/Qusetion', name: '问题反馈', component: Qusetion, meta: { keepAlive: true} },
    { path: '/QusetionList', name: '我的反馈', component: QusetionList },
    { path: '/AboutUs', name: '关于我们', component: AboutUs, meta: { keepAlive: true} },
    { path: '/About', name: '关于嗨币', component: About, meta: { keepAlive: true} },
    { path: '/Contact', name: '联系我们', component: Contact, meta: { keepAlive: true} },
    { path: '/Notice', name: '公告中心', component: Notice, meta: { keepAlive: true} },
    { path: '/Safe', name: '安全中心', component: Safe, meta: { keepAlive: true} },
    { path: '/ResetPassWord', name: '重置登陆密码', component: ResetPassWord, meta: { keepAlive: true} },
    { path: '/EmailVerify', name: '邮箱验证', component: EmailVerify, meta: { keepAlive: true} },
    { path: '/PhoneVerify', name: '手机验证', component: PhoneVerify, meta: { keepAlive: true} },
    { path: '/GoogleVerify', name: '谷歌验证', component: GoogleVerify, meta: { keepAlive: true} },
    { path: '/Property', name: '我的资产', component: Property, },
    { path: '/Recharge/:id', name: '充值', component: Recharge, props: true, },
    { path: '/Cash/:id', name: '提现', component: Cash, props: true,},
    { path: '/Historys/:id', name: '历史记录', component: Historys, props: true,},
    { path: '/Address', name: '地址', component: Address },
  ],
  linkActiveClass: 'router-link-active'
})



