<template>
  <div id="app">
    <mu-appbar v-if="!isIndex" style="width: 100%;" :title="topTitle">
      <mu-button icon @click="routerBack" slot="left">
        <span class="icon iconfont">&#xe647;</span>
      </mu-button>
    </mu-appbar>

    <!-- <transition :name="transitionName"> -->
    <keep-alive>
      <router-view class="router-view" v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <!-- </transition> -->
    <router-view class="router-view" v-if="!$route.meta.keepAlive"></router-view>

    <mu-container v-if="isBottom">
      <mu-bottom-nav>
        <router-link to="/Index" class="mu-bottom-item"><i class="icon iconfont">&#xe600;</i><span class="mu-bottom-item-text">行情</span></router-link>
        <router-link to="/Deal" class="mu-bottom-item"><i class="icon iconfont">&#xe687;</i><span class="mu-bottom-item-text">交易</span></router-link>
        <router-link to="/MyCenter" class="mu-bottom-item"><i class="icon iconfont">&#xe601;</i><span class="mu-bottom-item-text">我的</span></router-link>
      </mu-bottom-nav>
    </mu-container>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      transitionName: "slide-right"
    };
  },
  computed: {
    topTitle() {
      return this.$route.name || "";
    },
    isIndex() {
      return (
        this.$route.path === "/Index" ||
        this.$route.path === "/Login" ||
        this.$route.path.indexOf('Deal') != -1 ||
        this.$route.path === "/MyCenter" ||
        this.$route.path === "/Search" ||
        (this.$route.path.indexOf('Details') != -1 && this.$route.path.indexOf('EntrustDetails') == -1)
      );
    },
    isBottom() {
      return (
        this.$route.path === "/Index" ||
        this.$route.path.indexOf('Deal') != -1 ||
        this.$route.path === "/MyCenter"
      );
    }
  },
  watch: {
    $route(to, from) {
      let isBack = this.$router.isBack; //  监听路由变化时的状态为前进还是后退
      if (isBack) {
        this.transitionName = "slide-right";
      } else {
        this.transitionName = "slide-left";
      }
      this.$router.isBack = false;
    }
  },
  methods: {
    routerBack() {
      this.$router.back();
    }
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2A2D2D;
  font-weight: 500;
}
.mu-appbar{
  border-bottom: solid 1px #F5F5F5;
}
/* 路由切换动画 */
.router-view {
  position: absolute;
  width: 100%;
  transition: all 0.3s ease;
  padding-top: 50px;
}
.slide-left-enter,
.slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(100%, 0);
  transform: translate(100%, 0);
}
.slide-left-leave-active,
.slide-right-enter {
  opacity: 0;
  -webkit-transform: translate(-100%, 0);
  transform: translate(-100% 0);
}
</style>



