<template>
  <!-- 身份认证 -->
  <div class="card">
    <div class="tip">为了您的资金安全，需验证您的身份才可以进行其他操作；认证信息一经验证不能修改，请务必如实填写</div>
    <mu-form ref="form" :model="validateForm" class="card-content">
      <mu-form-item label="真实姓名：" prop="name" :rules="nameRules">
        <mu-text-field v-model="validateForm.name" prop="name"></mu-text-field>
      </mu-form-item>
      <mu-form-item label="身份证号码：" prop="cardId" :rules="cardIdRules">
        <mu-text-field v-model="validateForm.cardId" prop="cardId"></mu-text-field>
      </mu-form-item>
      <mu-form-item>
        <mu-button color="primary" @click="submit">确定</mu-button>
      </mu-form-item>
    </mu-form>
  </div>
</template>

<script>
export default {
  name: 'Card',
  data () {
    return {
      nameRules: [
        { validate: (val) => !!val, message: '请输入真实姓名：'},
      ],
      cardIdRules: [
        { validate: (val) => !!val, message: '请输入身份证号码'},
        { validate: (val) => (/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(val)), message: '请输入合法的身份证号码'}
      ],
      validateForm: {
        name: '',
        cardId: '',
      }
    }
  },
  methods: {
    submit() {
      this.$refs.form.validate().then((result) => {
        if(result){

        }
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



