<template>
  <div class="index">
    <div class="title">币种行情</div>
    <mu-container class="market" v-if="marketsArray.length>0">
      <mu-tabs :value.sync="active">
        <router-link to="/ChooseCoin">选择币种</router-link>
        <mu-tab v-for="item in quoteCoinArray" :key="item.id">{{item.name}}</mu-tab>
      </mu-tabs>
      <div v-if="active === index" v-for="(item, index) in quoteCoinCategoryArray" :key="index">
        <mu-paper :z-depth="1">
          <mu-data-table stripe :height="height" :columns="columns" :sort.sync="sort" @sort-change="handleSortChange(index,sort)" :data="item">
            <template slot-scope="scope">
              <td class="is-left" @click="toDetails(scope.row.id)">
                {{scope.row.baseCoin.name}}/{{scope.row.quoteCoin.name}}
                <span>{{scope.row.amount.toFixed(scope.row.amountPrecision)}}</span>
              </td>
              <td class="is-center" @click="toDetails(scope.row.id)">
                {{scope.row.close.toFixed(scope.row.pricePrecision)}}
                <span>￥{{scope.row.close.toFixed(scope.row.pricePrecision)}}</span>
              </td>
              <td class="is-right" @click="toDetails(scope.row.id)" v-if="scope.row.open !== 0">{{((scope.row.close/scope.row.open-1)*100).toFixed(2)}}%</td>
              <td class="is-right" @click="toDetails(scope.row.id)" v-else>0.00%</td>
            </template>
          </mu-data-table>
        </mu-paper>
      </div>
      <mu-button flat to="/Search"><span class="icon iconfont">&#xe603;</span></mu-button>
    </mu-container>
  </div>
</template>

<script>
export default {
  name: 'Index',
  data () {
    return {
      active: 0,
      sort: {
        name: '',
        order: 'asc'
      },
      coinTickers:[], //市场价格
      marketsArray: [], //市场交易对
      quoteCoinArray: [],  //切换标题数组
      quoteCoinCategoryArray: [], //内容列表数组
      height:"",
      columns: [
        { title: '24h成交量', name: 'count', align: 'left', sortable: true },
        { title: '最新价', name: 'open', align: 'center', sortable: true },
        { title: '24h涨跌', name: 'close', align: 'right', sortable: true },
      ],
    }
  },
  mounted() {
    this.getInfo();
    // this.height = document.querySelector(".mu-table-body-wrapper").offsetHeight - 90;
  },
  methods: {
    // 表格排序设置
    handleSortChange (index,{name, order}) {
      this.quoteCoinCategoryArray[index] = this.quoteCoinCategoryArray[index].sort((a, b) => order === 'asc' ? a[name] - b[name] : b[name] - a[name]);
    },
    getMarkets() {
      return this.$http.get('/api/services/app/Market/GetMarkets');
    },
    getTickers() {
      return this.$http
        .get('/api/services/app/Market/GetTickers', {
          params: {
            TimeZone: 'UTC',
          },
        });
    },
    // 请求数据
    getInfo() {
      this.$http.all([this.getMarkets(), this.getTickers()])
        .then(this.$http.spread((markets, tickers) => {
          this.coinTickers = tickers.data.result.items;
          this.marketsArray = markets.data.result.items;
          // 数据组装筛选
          const quoteCoinInfoArray = this.marketsArray.map(coin => coin.quoteCoin);
          const quoteCoinIdArray = this.marketsArray.map(coin => coin.quoteCoinId);
          const quoteCoinIdUniqueArray = Array.from(new Set(quoteCoinIdArray));
          this.quoteCoinArray = quoteCoinIdUniqueArray.map(coinId => quoteCoinInfoArray.find(element => element.id === coinId));
          this.quoteCoinCategoryArray = quoteCoinIdUniqueArray.map(coinId => this.marketsArray.filter(element => element.quoteCoinId === coinId));
          // 价格和交易对的数组组装
          this.quoteCoinCategoryArray.map((arr) => {
            arr.map( val => {
              this.coinTickers.map((val2) => {
                if(val.id===val2.marketId){
                  return Object.assign(val,val2);
                }
              })
            })
          });
          console.log(this.quoteCoinCategoryArray);
        }))
        .catch((error) => {
          this.$toast.message(error.response.data.error.message);
        });
    },
    // 跳转详情
    toDetails(id) {
      this.$router.push(`/Details/${id}`)
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.router-view{ padding-top: 0;}
.title{ width: 100%; background: #38BAB8; height: 50px; line-height: 50px; text-align: center; font-size: 18px; color: #fff;}
</style>



