<template>
  <div class="details">
    <div class="top" v-if="market.baseCoin">
      <div class="title">{{market.baseCoin.name}}/{{market.quoteCoin.name}}</div>
      <mu-button icon @click="routerBack" slot="left">
        <span class="icon iconfont">&#xe647;</span>
      </mu-button>
      <span class="icon iconfont" :class="{isChoose: info.isChoose}" @click="changeChoose(info.id)">&#xe627;</span>
    </div>
    <!-- info -->
    <div class="details-bg" v-if="market.baseCoin">
      <div class="details-info">
        <div class="left">
          <h4>
            {{ticker.close.toFixed(market.pricePrecision)}}&nbsp;
            <!-- <span>¥{{ticker.close.toFixed(market.pricePrecision)}}</span> -->
          </h4>
          <p>
            <span v-if="ticker.open !== 0">涨幅 {{((ticker.close/ticker.open-1)*100).toFixed(2)}}%</span>
            <span v-else>涨幅 0.00%</span>
            &nbsp;&nbsp;&nbsp;&nbsp;<span>成交量 {{market.quoteCoin.name}} {{ticker.amount.toFixed(market.amountPrecision)}}</span>
          </p>
        </div>
        <div class="right">
          <div><span>24小时最高</span><br><span>{{ticker.high.toFixed(market.amountPrecision)}}</span></div>
          <div><span>24小时最低</span><br><span>{{ticker.low.toFixed(market.amountPrecision)}}</span></div>
        </div>
      </div>
    </div>
    <!-- k线图 -->
    <div id="kcharts" class="chart" style="height: 266px"></div>
    <!-- tabs -->
    <div class="details-tabs">
      <mu-tabs :value.sync="active1" inverse color="secondary" text-color="rgba(0, 0, 0, .54)"  center>
        <mu-tab>委托订单</mu-tab>
        <mu-tab>最新成交</mu-tab>
      </mu-tabs>
      <div v-if="active1 === 0">
        <div class="show">
          <div><span>买</span><span>卖</span></div>
          <mu-select v-model="fractional" :change="fractionalChange()">
            <mu-option v-for="(item,index) in fractionalList" :key="index" :label="item" :value="item"></mu-option>
          </mu-select>
        </div>
        <div class="deal">
          <!-- 买 -->
          <ul>
            <li><div>价格</div><div><span>交易额</span></div></li>
            <li v-if="item.direction == 0" v-for="item in entrustOrders" :key="item.id">
              <div>{{item.price}}</div>
              <div><span>{{item.amount}}</span></div>
            </li>
          </ul>
          <!-- 卖 -->
          <ul>
            <li><div>价格</div><div><span>数量</span></div></li>
            <li v-if="item.direction == 1" v-for="item in entrustOrders" :key="item.id">
              <div>{{item.amount}}</div>
              <div><span>{{item.price}}</span></div>
            </li>
          </ul>
        </div>
        
      </div>
      <div class="newDeal" v-if="active1 === 1">
        <ul>
          <li class="title"><span>成交时间</span><span>成交价格</span><span>成交数量</span></li>
          <li v-for="item in trades" :key="item.id">
            <span>{{item.creationTime}}</span><span>{{item.price}}</span><span>{{item.quantity}}</span>
          </li>
        </ul>
      </div>
    </div>
    <!-- sell -->
    <mu-bottom-nav class="bottom-buy">
      <router-link :to="`/Deal/${id}`" class="mu-bottom-item">买入</router-link>
      <router-link :to="`/Deal/${id}`" class="mu-bottom-item">卖出</router-link>
    </mu-bottom-nav>
  </div>
</template>

<script>
import formatDate from './bin/date.js';
const echarts = require('echarts');
export default {
  name: 'Details',
  data () {
    return {
      active1: 0,
      info: [],
      fractional:"6位小数",
      fractionalList:["6位小数","5位小数","4位小数","3位小数"],
      market: {}, //当前交易对的信息
      ticker: {}, // 当前交易对的价格信息
      entrustOrders:[], // 委托订单
      trades: {}, // 最新成交
      kData:[], //k线图数据
      kTime:[], // k线图时间数据
      kData2:[], //k线图数据
      kTime2:[], // k线图时间数据
    }
  },
  props:['id'],
  created() {
    this.getInfo();
  },
  methods: {
    createdEchart(){
        var dom = document.getElementById("kcharts");
        var myChart = echarts.init(dom);
        var app = {};
        var option = null;
        var colorList = ['#c23531', '#2f4554', '#61a0a8', '#d48265', '#91c7ae', '#749f83', '#ca8622', '#bda29a', '#6e7074', '#546570', '#c4ccd3'];
        var labelFont = 'bold 12px Sans-serif';
        let dates = this.kTime;  //k线时间
        let data = this.kData;   //k线数据
        option = {
            animation: false,
            color: colorList,
            title: {
                left: 'center',
                text: ''
            },
            legend: {
                top: 30,
                data: ['日K', 'MA5', 'MA10', 'MA20', 'MA30']
            },
            // 提示框
            tooltip: {
                triggerOn: 'none',
                transitionDuration: 0,
                confine: true,
                bordeRadius: 4,
                borderWidth: 1,
                borderColor: '#333',
                backgroundColor: 'rgba(255,255,255,0.9)',
                textStyle: {
                    fontSize: 12,
                    color: '#333'
                },
                position: function (pos, params, el, elRect, size) {
                    var obj = {
                        top: 40
                    };
                    obj[['left', 'right'][+(pos[0] < size.viewSize[0] / 2)]] = 5;
                    return obj;
                }
            },
            axisPointer: {
                link: [{
                    xAxisIndex: [0, 1]
                }]
            },
            // 缩放
            dataZoom: [{
                type: 'slider',
                xAxisIndex: [0, 1],
                realtime: false,
                start: 30,
                end: 70,
                top: 20,
                height: 20,
                handleIcon: 'M10.7,11.9H9.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4h1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z',
                handleSize: '100%'
            }, {
                type: 'inside',
                xAxisIndex: [0, 1],
                start: 40,
                end: 70,
                top: 30,
                height: 20
            }],
            xAxis: [{
                type: 'category',
                data: dates,
                boundaryGap: false,
                axisLine: { lineStyle: { color: '#777' } },
                axisLabel: {
                    formatter: function (value) {
                        return echarts.format.formatTime('MM-dd', value);
                    }
                },
                min: 'dataMin',
                max: 'dataMax',
                axisPointer: {
                    show: true
                }
            }, {
                type: 'category',
                gridIndex: 1,
                data: dates,
                scale: true,
                boundaryGap: false,
                splitLine: { show: false },
                axisLabel: { show: false },
                axisTick: { show: false },
                axisLine: { lineStyle: { color: '#777' } },
                splitNumber: 20,
                min: 'dataMin',
                max: 'dataMax',
                axisPointer: {
                    type: 'shadow',
                    label: { show: false },
                    triggerTooltip: true,
                    handle: {
                        show: true,
                        margin: 30,
                        color: '#B80C00'
                    }
                }
            }],
            yAxis: [{
                scale: true,
                splitNumber: 2,
                axisLine: { lineStyle: { color: '#777' } },
                splitLine: { show: true },
                axisTick: { show: false },
                axisLabel: {
                    inside: true,
                    formatter: '{value}\n'
                }
            }, {
                scale: true,
                gridIndex: 1,
                splitNumber: 2,
                axisLabel: { show: false },
                axisLine: { show: false },
                axisTick: { show: false },
                splitLine: { show: false }
            }],
            grid: [{
                left: 20,
                right: 20,
                top: 60,
                height: 150
            }, {
                left: 20,
                right: 20,
                height: 30,
                top: 180
            }],
            series: [{
                type: 'candlestick',
                name: 'Kline',
                data: data,
                itemStyle: {
                    normal: {
                        color: '#ef232a',
                        color0: '#14b143',
                        borderColor: '#ef232a',
                        borderColor0: '#14b143'
                    },
                    emphasis: {
                        color: 'black',
                        color0: '#444',
                        borderColor: 'black',
                        borderColor0: '#444'
                    }
                }
            }]
        };
        ;
        if (option && typeof option === "object") {
            myChart.setOption(option, true);
        }
    },
    routerBack() {
      this.$router.back();
    },
    getMarkets() {
      return this.$http.get('/api/services/app/Market/GetMarkets');
    },
    getTickers() {
      return this.$http
        .get('/api/services/app/Market/GetTickers', {
          params: {
            TimeZone: 'UTC',
          },
        });
    },
    // 请求数据
    getInfo() {
      this.$http.all([this.getMarkets(), this.getTickers()])
        .then(this.$http.spread((markets, tickers) => {
            const coinTickers = tickers.data.result.items;
            const marketsArray = markets.data.result.items;
            this.market = marketsArray.find((item, index, arr) => {
            return item.id == this.id
            })
            this.ticker = coinTickers.find((item, index, arr) => {
            return item.marketId == this.id
            })
            // console.log(this.market)
            // console.log(this.ticker)
            this.getKLine();
            this.getEntrustOrders();
            this.getTrades();
        }))
        // .catch((error) => {
        //     this.$toast.message(error.response.data.error.message);
        // });
    },
    // k线图数据
    getKLine() {
      var fromTime = new Date();  
      var endTime = new Date();
      fromTime.setHours(fromTime.getHours(), fromTime.getMinutes() - fromTime.getTimezoneOffset());
      // 6个月前时间
      fromTime.setMonth(fromTime.getMonth()-6);
      endTime.setHours(endTime.getHours(), endTime.getMinutes() - endTime.getTimezoneOffset());
      fromTime = fromTime.toISOString();
      endTime = endTime.toISOString();
      this.$http(`/api/services/app/Market/GetKLine?marketId=${this.ticker.marketId}&from=${fromTime}&to=${endTime}&TimeZone=UTC&size=1D`).then((response) => {
        console.log(response.data.result.items);
        let kInfo = response.data.result.items;
        // k线时间数据
        kInfo.forEach((item) => {
          let time = formatDate(new Date(item.time), 'yyyy-MM-dd')
          this.kTime.push(time);
        });    
        kInfo.forEach((item) => {
          this.kData.push([item.open,item.close,item.low,item.high])
        });
        this.kTime.reverse();
        this.kData.reverse();
        this.createdEchart();
      })
    },
    // 当前委托订单
    getEntrustOrders() {
      const param = {
        marketTenantId: this.ticker.marketTenantId,
        marketId: this.ticker.marketId,
        skipCount: 0,
        maxResultCount: 10,
      }; 
      this.$http("/api/services/app/Trade/GetMyOrders", param).then((response) => {
        this.entrustOrders = response.data.result.items;
        this.entrustOrders.map((element) => {
          const date = new Date(element.creationTime);
          element.time = `${date.getFullYear()}/${(`00${date.getMonth() + 1}`.slice(-2))}/${(`00${date.getDate()}`.slice(-2))} ${(`00${date.getHours()}`.slice(-2))}:${(`00${date.getMinutes()}`.slice(-2))}:${(`00${date.getSeconds()}`.slice(-2))}`;
          return element;
        });
        this.entrustOrders = this.entrustOrders.filter((item) => {
            return item.marketId == this.ticker.marketId
        })
      })
    },
    // 最新成交
    getTrades() {
      const time = new Date().toISOString();
      this.$http.get(`/api/services/app/Market/GetTrades?marketId=${this.ticker.marketId}&time=${time}&marketTenantId=1`)
      .then((response) => {
        // console.log(response.data.result)
        this.trades = response.data.result.items.reverse();
        this.trades.map((element) => {
          const date = new Date(element.creationTime);
          element.time = `${(`00${date.getHours()}`.slice(-2))}:${(`00${date.getMinutes()}`.slice(-2))}:${(`00${date.getSeconds()}`.slice(-2))}`;
          return element;
        });
      })
    },
    changeChoose(index) {
      // 是否选择
    },
    // 小数选择
    fractionalChange(){
      // console.log(this.fractional)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.top>span{ position: absolute; right: 20px; top: 12px; color: #868686; font-size: 18px;}
</style>



