<template>
  <!-- 历史委托 -->
  <div class="entrust">
    <mu-button class="" icon @click="showSearch" slot="left">
      <!-- <span class="icon iconfont">&#xe706;</span> -->
    </mu-button>
    <ul>
      <li v-for="item in entrustOrders" :key="item.id">
        <router-link :to="`/EntrustDetails/${item.id}`">
          <h4>
            <span v-if="item.direction === 0">买入</span>
            <span v-if="item.direction === 1">卖出</span>
            {{item.baseCoinName}}/{{item.quoteCoinName}}
          </h4>
          <div class="title"><span>时间</span><span>委托价（{{item.baseCoinName}}）</span></div>
          <div><span>{{item.time}}</span><span>{{item.price}}</span></div>
          <div class="title"><span>完成数量</span><span>委托总额（{{item.baseCoinName}}）</span></div>
          <div><span>{{item.completedAmount}}</span><span>{{item.amount}}</span>
          </div>
        </router-link>
      </li>
    </ul>  
  </div>
</template>

<script>
export default {
  name: 'Entrust',
  data () {
    return {
      entrustOrders: []
    }
  },
  // props:['marketTenantId','marketId'],
  created() {
    this.getEntrustOrders();
  },
  methods: {
    // 历史委托订单
    getEntrustOrders() {
      const param = {
        // marketTenantId: this.marketTenantId,
        // marketId: this.marketId,
        skipCount: 0,
        maxResultCount: 10,
      }; 
      this.$http("/api/services/app/Trade/GetMyOrderHistories", param).then((response) => {
        this.entrustOrders = response.data.result.items;
        this.entrustOrders.map((element) => {
          const date = new Date(element.creationTime);
          element.time = `${date.getFullYear()}/${(`00${date.getMonth() + 1}`.slice(-2))}/${(`00${date.getDate()}`.slice(-2))} ${(`00${date.getHours()}`.slice(-2))}:${(`00${date.getMinutes()}`.slice(-2))}:${(`00${date.getSeconds()}`.slice(-2))}`;
          return element;
        });
        console.log(this.entrustOrders)
      })
      .catch((error) => {
        this.$toast.message(error.response.data.error.message);
      });
    },
    // 弹出搜索
    showSearch(){

    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



