<template>
  <div class="entrustDetails">
    <div class="entrustDetails-top">
      <h4><span>买入</span>{{info2.baseCoinName}}/{{info2.quoteCoinName}}</h4>
      <div class="title"><span>时间</span><span>委托价（{{info2.baseCoinName}}）</span></div>
      <div><span>{{info2.time}}</span><span>{{info2.price}}</span></div>
      <div class="title"><span>完成数量</span><span>委托总额（{{info2.baseCoinName}}）</span></div>
      <div><span>{{info2.completedAmount}}</span><span>{{info2.amount}}</span></div>
    </div>
    <ul>
      <li>
        <div><span>是否完成：</span><span v-if="info.isCompleted">是</span><span v-if="!info.isCompleted">否</span></div>
        <div><span>创建时间：</span><span>{{info.creationTime}}</span></div>
        <div><span>价格：</span><span>{{info.price}}</span></div>
        <div><span>数量：</span><span>{{info.amount}}</span></div>
        <div v-if="info.feeAmount"><span>手续费：</span><span>{{info.feeAmount}}</span></div>
        <div v-if="!info.isCompleted"><span>取消时间：</span><span>{{info.cancelTime}}</span></div>
      </li>
    </ul>  
  </div>
</template>

<script>
import formatDate from './bin/date.js';
export default {
  name: 'EntrustDetails',
  data () {
    return {
      info: {},
      info2: {}
    }
  },
  props:['id'],
  created() {
    this.getInfo();
    this.getEntrustOrders();
  },
  methods: {
    getInfo() {
      this.$http.get(`/api/services/app/Trade/GetMyOrderDetails?entrustId=${this.id}`)
      .then((response) => {
        this.info = response.data.result.item;
        this.info.creationTime = formatDate(new Date(this.info.creationTime), 'yyyy-MM-dd hh:mm:ss');
        this.info.cancelTime = formatDate(new Date(this.info.cancelTime), 'yyyy-MM-dd hh:mm:ss');
      })
      .catch((error) => {
        this.$toast.message(error.response.data.error.message);
      });
    },
    // 历史委托订单
    getEntrustOrders() {
      const param = {
        skipCount: 0,
        maxResultCount: 10,
      }; 
      this.$http("/api/services/app/Trade/GetMyOrderHistories", param).then((response) => {
        this.entrustOrders = response.data.result.items;
        this.entrustOrders.map((element) => {
          const date = new Date(element.creationTime);
          element.time = `${date.getFullYear()}/${(`00${date.getMonth() + 1}`.slice(-2))}/${(`00${date.getDate()}`.slice(-2))} ${(`00${date.getHours()}`.slice(-2))}:${(`00${date.getMinutes()}`.slice(-2))}:${(`00${date.getSeconds()}`.slice(-2))}`;
          return element;
        });
        this.info2 = this.entrustOrders.find((item) => {
          return item.id == this.id
        })
      })
      .catch((error) => {
        this.$toast.message(error.response.data.error.message);
      });
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



