<template>
  <!-- 身份认证 -->
  <div class="address">
    <mu-form ref="form" :model="validateForm" class="card-content">
      <mu-form-item label="请输入备注信息" prop="remarks" :rules="remarksRules">
        <mu-text-field v-model="validateForm.remarks" prop="remarks"></mu-text-field>
      </mu-form-item>
      <mu-form-item class="address-info" label="请输入或长按粘贴地址" prop="address" :rules="addressRules">
        <mu-text-field v-model="validateForm.address" prop="address"></mu-text-field>
        <span class="icon iconfont" @click="scan">&#xe621;</span>
      </mu-form-item>
      <mu-form-item>
        <mu-button color="primary" @click="submit">确定</mu-button>
      </mu-form-item>
    </mu-form>
  </div>
</template>

<script>
export default {
  name: 'Address',
  data () {
    return {
      remarksRules: [
        { validate: (val) => !!val, message: '请输入备注信息'},
      ],
      addressRules: [
        { validate: (val) => !!val, message: '请输入地址'},
      ],
      validateForm: {
        remarks: '',
        address: '',
      }
    }
  },
  methods: {
    scan() {

    },
    submit() {
      this.$refs.form.validate().then((result) => {
        console.log('form valid: ', result)
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.address-info .mu-form-item-content{ position: relative;}
.address-info .mu-form-item-content>span{ position: absolute; right: 15px; top: 15px;}
</style>



