<template>
  <div class="property">
    <div class="property-top">
      <div class="title">我的资产</div>
      <div class="number">{{firstCoin.banlance}}<span>&nbsp;{{firstCoin.name}}</span></div>
      <div>&nbsp;<br></div>
      <!-- <div>≈￥1,256.89<br></div> -->
    </div>
    <div class="property-tip">
      <mu-text-field v-model="keyword" placeholder="请搜索币种"></mu-text-field>
      <!-- <mu-button><span class="icon iconfont">&#xe603;</span>请搜索币种</mu-button> -->
      <mu-flex class="select-control-row">
        <mu-switch v-model="switchVal" label="隐藏0资金币种" label-left></mu-switch>
      </mu-flex>
    </div>
    <mu-list>
      <mu-list-item avatar button @click="choose(item.id)" v-for="item in searchFilter" :key="item.id">
        <mu-list-item-action>
          <img src="../../assets/images/logo.png" alt="">
        </mu-list-item-action>
        <mu-list-item-title>{{item.virtualCoin.name}}</mu-list-item-title>
        <mu-list-item-action class="center">
          <span>{{item.banlance.toFixed(item.virtualCoin.decimals)}}<br>
            <!-- <font>≈0.0000CNY</font> -->
          </span>
        </mu-list-item-action>
        <mu-list-item-action>
          <span class="icon iconfont">&#xe63f;</span>
        </mu-list-item-action>
      </mu-list-item>
    </mu-list>
    <!-- 底部弹出 -->
    <mu-bottom-sheet :open.sync="open">
      <mu-list>
        <mu-list-item button @click="recharge()">
          <mu-list-item-title>充值</mu-list-item-title>
        </mu-list-item>
        <mu-list-item button @click="cash()">
          <mu-list-item-title>提现</mu-list-item-title>
        </mu-list-item>
        <mu-list-item button @click="historys()">
          <mu-list-item-title>历史记录</mu-list-item-title>
        </mu-list-item>
        <mu-list-item button @click="closeBottomSheet()">
          <mu-list-item-title>取消</mu-list-item-title>
        </mu-list-item>
      </mu-list>
    </mu-bottom-sheet>
  </div>
</template>

<script>
export default {
  name: 'Property',
  data () {
    return {
      switchVal: false,
      open: false,
      virtualCoinList: [],
      firstCoin: {name:"", banlance:'0.00000000'},
      decimals: 2,
      id:"",
      keyword:""
    }
  },
  computed: {
    //搜索过滤
    searchFilter(){
      return this.virtualCoinListFilter.filter((item)=>{
        return item.virtualCoin.name.match(this.keyword.toUpperCase()) || item.virtualCoin.name.match(this.keyword.toUpperCase());
      })
    },
    // 过滤0资金
    virtualCoinListFilter() {
      return this.virtualCoinList.filter((item) => {
        let flag = true;
        if (this.switchVal) {
          flag = item.banlance && item.banlance !== 0;
        }
        return flag;
      });
    },
  },
  created() {
    this.getInfo();
  },
  methods: {
    getInfo() {
      this.$http.get('/api/services/app/Wallet/GetMyWalletBalances')
      .then((wallet) => {
        this.virtualCoinList = wallet.data.result.items;
        for(let i=0; i<this.virtualCoinList.length; i++) {
          if(this.virtualCoinList[i].banlance){
            this.firstCoin.banlance = this.virtualCoinList[i].banlance.toFixed(this.virtualCoinList[i].virtualCoin.decimals);
            this.firstCoin.name = this.virtualCoinList[i].virtualCoin.name;
            break;
          }
        }
        console.log(this.virtualCoinList)
      })
      // .catch((error) => {
      //   this.$toast.message(error.response.data.error.message);
      // });
    },
    // 底部弹出控制
    choose(id) {
      // 可以做一个判断，如果不允许充值，不打开弹窗allowDeposit，allowWithdraw，或者在跳转后的页面进行提示，按钮禁用。
      this.open = true;
      this.id = id;
    },
    closeBottomSheet () {
      this.open = false;
    },
    // 充值
    recharge() {
      this.open = false;
      this.$router.push(`/Recharge/${this.id}`)
    },
    // 提现
    cash() {
      this.open = false;
      this.$router.push(`/Cash/${this.id}`)
    },
    // 充值
    historys() {
      this.open = false;
      this.$router.push(`/Historys/${this.id}`)
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



