<template>
  <div class="phoneVerify">
    <mu-form ref="form" :model="validateForm" class="card-content">
      <mu-form-item label="请输入手机号码" prop="email" :rules="emailRules">
        <mu-text-field type="number" v-model="validateForm.email" prop="email"></mu-text-field>
      </mu-form-item>
      <mu-form-item label="短信验证码" prop="smscode" :rules="codeRules">
        <!-- <mu-text-field v-model="validateForm.code" prop="code"></mu-text-field> -->
        <sms-code v-model="validateForm.smscode" :phone="validateForm.username" @getCode="getCode" prop="smscode"></sms-code>
      </mu-form-item>
      <mu-form-item>
        <mu-button color="primary" @click="submit">启用手机验证</mu-button>
      </mu-form-item>
    </mu-form>
  </div>
</template>

<script>
import smsCode from "@/components/bin/smsCode";
export default {
  name: 'PhoneVerify',
  components: {
    smsCode
  },
  data () {
    return {
      emailRules: [
        { validate: (val) => !!val, message: '请输入手机号码'},
      ],
      codeRules: [
        { validate: (val) => !!val, message: '请输入短信验证码'},
      ],
      validateForm: {
        email: '',
        smscode: '',
      },
      codeMsg:'获取验证码',
      sended:false,
      timer:null,
      startTime:null,
    }
  },
  methods: {
    getCode() {
      let app = this;
      this.$http({
        // url: app.$lib.url + "index/sendSms",
        // data: {
        //   // act: "send_code",
        //   phone: app.username
        // },
        // success: function(r) {
        //   if (r.status == "y") {
        //     app.serverCode = r.data;
        //     localStorage.serverCode = r.data;
        //   } else {
        //     app.$layer.open({ time: 3, skin: "msg", content: r.info });
        //   }
        // }
      });
    },
    submit() {
      this.$refs.form.validate().then((result) => {
        if(result) {

        }
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.phoneVerify button{margin: 20px 0 0 0;}
</style>



