<template>
  <div class="resetPassWord">
    <mu-form ref="form" :model="validateForm" class="card-content">
      <mu-form-item label="输入旧密码" prop="oldPassWord" :rules="oldPassWordRules">
        <mu-text-field type="password" v-model="validateForm.oldPassWord" prop="oldPassWord"></mu-text-field>
      </mu-form-item>
      <mu-form-item label="输入新密码" prop="newPassWord" :rules="newPassWordRules">
        <mu-text-field type="password" v-model="validateForm.newPassWord" prop="newPassWord"></mu-text-field>
      </mu-form-item>
      <mu-form-item label="确认新密码" prop="newPassWord2" :rules="newPassWord2Rules">
        <mu-text-field type="password" v-model="validateForm.newPassWord2" prop="newPassWord2"></mu-text-field>
      </mu-form-item>
      <mu-form-item>
        <mu-button color="primary" @click="submit">确定</mu-button>
      </mu-form-item>
    </mu-form>
    <div class="tip">
      <p class="title">温馨提示：</p>
      <p class="value">1.请不要设置之前用过的旧密码，否则密码修改不成功。</p>
      <p class="value">2.请不要频繁修改密码，每日最多可修改3次。</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "CaResetPassWordrd",
  data() {
    return {
      oldPassWordRules: [{ validate: val => !!val, message: "请输入旧密码" }],
      newPassWordRules: [
        { validate: val => !!val, message: "请输入新密码" },
        { validate: val => val.length >= 6 && val.length <= 20, message: "密码长度大于等于6小于20"}
      ],
      newPassWord2Rules: [
        { validate: val => !!val, message: "请输入确认密码" },
        { validate: val => (val == this.validateForm.newPassWord), message: "2次密码不一致"}
      ],
      validateForm: {
        oldPassWord: "",
        newPassWord: "",
        newPassWord2: ""
      }
    };
  },
  methods: {
    submit() {
      this.$refs.form.validate().then(result => {
        if (result) {
          const param = {
            currentPassword: this.validateForm.oldPassWord,
            newPassword: this.validateForm.newPassWord,
          };
          this.$http.post('/api/services/app/Profile/ChangePassword', param)
          .then((response) => {
            if (response.data.success) {
              this.$toast.message("修改成功");
              setTimeout(() => {
                this.$router.push('/MyCenter');
              }, 2000);
            }
          })
          .catch(error => {
            this.$toast.message(error.response.data.error.message);
          });
        }
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



