<template>
  <div class="search">
    <div class="top">
      <mu-text-field v-model="keyword" placeholder="请输入搜索币种"></mu-text-field>
      <mu-button icon @click="routerBack" slot="left">
        <span class="icon iconfont">&#xe647;</span>
      </mu-button>
      <span class="icon iconfont" @click="clear">&#xe603;</span>
    </div>
    <!-- <div class="recordTitle">历史记录：<span class="icon iconfont">&#xe676;</span></div> -->
    <ul class="choose-ul">
      <li v-for="(item) in searchFilter" :key="item.id">
        <router-link :to="`/Details/${item.id}`">
          <span>{{item.baseCoin.name}}/{{item.quoteCoin.name}}</span>
        </router-link>
        <span class="icon iconfont" @click="changeChoose($event)">&#xe627;</span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Search',
  data () {
    return {
      keyword: '',
      marketsArray: [], //市场交易对
      isChoose:false  // 是否收藏
    }
  },
  computed:{
    //搜索过滤
    searchFilter(){
      return this.marketsArray.filter((market)=>{
        return market.baseCoin.name.match(this.keyword.toUpperCase()) || market.quoteCoin.name.match(this.keyword.toUpperCase());
      })
    }
  },
  mounted() {
    this.getInfo();
  },
  methods: {
    routerBack() {
      this.$router.back();
    },
    getMarkets() {
      return this.$http.get('/api/services/app/Market/GetMarkets');
    },
    // 请求数据
    getInfo() {
      this.$http.all([this.getMarkets()])
        .then(this.$http.spread((markets) => {
          this.marketsArray = markets.data.result.items;
          // console.log(this.marketsArray)
        }))
        .catch((error) => {
          this.$toast.message(error.response.data.error.message);
        });
    },    
    clear() {
      // 清除
    },
    changeChoose(event) {
      if(!event.target.style.color){
        event.target.style.color = '#FF3C00'
      }else{
        event.target.style.color = ''
      }
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/* .search .choose-ul li{ border-top: 0; border-bottom: solid 1px rgba(191,191,191,0.3);} */
</style>



