<template>
  <div class="myCenter">
    <div class="myCenter-top">
      <div class="loginOut" @click="loginOut"><span class="icon iconfont">&#xe65b;</span></div>
      <div class="phone">{{user.emailAddress}}</div>
      <p>最后登陆时间：{{lastLoginTime}}</p>
    </div>
    <mu-list>
      <mu-list-item avatar button to="/Property">
        <mu-list-item-action>
          <span class="icon iconfont color">&#xe93e;</span>
        </mu-list-item-action>
        <mu-list-item-title>我的资产</mu-list-item-title>
        <mu-list-item-action>
          <span class="icon iconfont">&#xe695;</span>
        </mu-list-item-action>
      </mu-list-item>
      <mu-list-item avatar button to="/Card">
        <mu-list-item-action>
          <span class="icon iconfont color">&#xe634;</span>
        </mu-list-item-action>
        <mu-list-item-title>身份认证</mu-list-item-title>
        <mu-list-item-action>
          <span class="icon iconfont">&#xe695;</span>
        </mu-list-item-action>
      </mu-list-item>
      <mu-list-item avatar button to="/Safe">
        <mu-list-item-action>
          <span class="icon iconfont color">&#xe895;</span>
        </mu-list-item-action>
        <mu-list-item-title>安全中心</mu-list-item-title>
        <mu-list-item-action>
          <span class="icon iconfont">&#xe695;</span>
        </mu-list-item-action>
      </mu-list-item>
      <mu-list-item avatar button to="/Notice">
        <mu-list-item-action>
          <span class="icon iconfont color">&#xe678;</span>
        </mu-list-item-action>
        <mu-list-item-title>公告中心</mu-list-item-title>
        <mu-list-item-action>
          <span class="icon iconfont">&#xe695;</span>
        </mu-list-item-action>
      </mu-list-item>
      <mu-list-item avatar button to="/AboutUs">
        <mu-list-item-action>
          <span class="icon iconfont color">&#xe615;</span>
        </mu-list-item-action>
        <mu-list-item-title>关于我们</mu-list-item-title>
        <mu-list-item-action>
          <span class="icon iconfont">&#xe695;</span>
        </mu-list-item-action>
      </mu-list-item>
      <mu-list-item avatar button to="/Qusetion">
        <mu-list-item-action>
          <span class="icon iconfont color">&#xe612;</span>
        </mu-list-item-action>
        <mu-list-item-title>问题反馈</mu-list-item-title>
        <mu-list-item-action>
          <span class="icon iconfont">&#xe695;</span>
        </mu-list-item-action>
      </mu-list-item>
    </mu-list>
  </div>
</template>

<script>
import formatDate from '../bin/date.js';
export default {
  name: 'MyCenter',
  data () {
    return {
      user: {
        emailAddress: '',
        id: '',
        userCode: '',
        userName: '',
      },
      lastLoginTime: ''
    }
  },
  created() {
    this.getInfo();
  },
  methods: {
    getInfo() {
      this.$http('/api/services/app/Session/GetCurrentLoginInformations')
      .then((response) => {
        const userInfo = response.data.result.user;
        // 保存个人信息数据
        sessionStorage.setItem('userInfo', JSON.stringify(userInfo));
        this.user.emailAddress = userInfo.emailAddress;
        this.user.id = userInfo.id;
        // this.user.userCode = userInfo.userName;
        // this.user.userName = userInfo.surname + userInfo.name;
      })
      .catch(error => {
        this.$toast.message(error.response.data.error.message);
      });
      // 请求登录日志
      this.$http.get('/api/services/app/UserLogin/GetRecentUserLoginAttempts')
      .then((response) => {
        const loginRecord = response.data.result.items;
        if (loginRecord && loginRecord.length > 0) {
          this.lastLoginTime = formatDate(
            new Date(loginRecord[0].creationTime),
            'yyyy-MM-dd hh:mm:ss',
          );
        }
      })
      .catch((error) => {
        this.$toast.message(error.response.data.error.message);
      });
    },
    // 退出登陆
    loginOut() {
      sessionStorage.removeItem('accessToken');
      sessionStorage.removeItem('userInfo');
      this.$router.push('/Login');
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.myCenter{ padding-top: 0;}
</style>



