<template>
  <div class="emailVerify">
    <mu-form ref="form" :model="validateForm" class="card-content">
      <mu-form-item label="请输入邮箱地址" prop="email" :rules="emailRules">
        <mu-text-field type="email" v-model="validateForm.email" prop="email"></mu-text-field>
      </mu-form-item>
      <mu-form-item label="请输入邮箱验证码" prop="smscode" :rules="codeRules">
        <!-- <mu-text-field v-model="validateForm.code" prop="code"></mu-text-field> -->
        <sms-code v-model="validateForm.smscode" :phone="validateForm.username" @getCode="getCode" prop="smscode"></sms-code>
      </mu-form-item>
      <mu-form-item>
        <mu-button color="primary" @click="submit">启用邮箱验证</mu-button>
      </mu-form-item>
    </mu-form>
    <div class="tip"><font>注意：</font>二次验证邮箱需与注册邮箱一致。</div>
  </div>
</template>

<script>
import smsCode from "@/components/bin/smsCode";
export default {
  name: 'EmailVerify',
  components: {
    smsCode
  },
  data () {
    return {
      emailRules: [
        { validate: (val) => !!val, message: '请输入邮箱地址'},
      ],
      codeRules: [
        { validate: (val) => !!val, message: '请输入邮箱验证码'},
      ],
      validateForm: {
        email: '',
        smscode: '',
      },
      codeMsg:'获取验证码',
      sended:false,
      timer:null,
      startTime:null,
    }
  },
  methods: {
    getCode() {
      let app = this;
      this.$http({
        
      });
    },
    submit() {
      this.$refs.form.validate().then((result) => {
        if(result) {

        }
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.tip{ text-align: center; color: #A8A9A9; font-size: 12px;}
.tip font{ color: #FF3C00; }
.emailVerify button{margin: 20px 0 0 0;}
</style>



